		let month = ts.date.getMonth()+1;
		let nextmonth=month+1;
		if(nextmonth>12){nextmonth=1}
		ts.month.push(month);ts.month.push(nextmonth);
		for(let i=1;i<60;i++){ts.minute.push(i)};
