		if(that.monthIndex==1){
			that.day=[];
			i=1;
		}else{
			that.day=[];
			i=that.date.getDate()+1;
		};
		
		if(that.Rmonth==4||that.Rmonth==6||that.Rmonth==9||that.Rmonth==11){
			for(i;i<=30;i++){
				
